<template>
    <div class="market-foot">
        <ul class="footer clearfix">
            <li class="fl" :class="{isSelected:index == parseInt(isClass)-3}" v-for="(item,index) in tabbar.font" :key="item.id" @click="tab(index)">
                <div class="icon"><img :src="tabbar.Icon[index]"></div>
                <div class="icon active"><img :src="tabbar.isIcon[index]"></div>
                <div class="font">{{item}}</div>
            </li>
        </ul>
    </div>
</template>
<script>
    export default {
        name: 'market-foote',
        data() {
            return {
                isClass:sessionStorage.getItem('router_index'),
                tabbar:{
                    isIcon:[require('@/assets/goods_m2.png'),require('@/assets/goods_c2.png'),require('@/assets/goods_g2.png'),require('@/assets/class_x2.png'),require('@/assets/cart_x2.png')],
                    Icon:[require('@/assets/goods_m1.png'),require('@/assets/goods_c1.png'),require('@/assets/goods_g1.png'),require('@/assets/class_x1.png'),require('@/assets/nav_cart.png')],
                    font:['聚清秒杀','超市首页','拼购','超市分类','购物车']
                }
            } 
        },
        methods:{
            tab(index){
                switch(index){
                    case 0:
                        this.$router.push({
                            path:'/home'
                        });
                        break;
                    case 1:
                        this.$router.push({
                            path:'/marketHome'
                        });
                        break;
                    case 2:
                        this.$router.push({
                            path:'/groupList'
                        });
                        break;
                    case 3:
                        this.$router.push({
                            path:'/marketClass'
                        });
                        break;
                    case 4:
                        this.$router.push({
                            path:'/cart'
                        });
                        break;
                    }
                }
            },
        watch:{
            '$route'(to,from){
                this.isClass = sessionStorage.getItem('router_index');
            }
        },
        created(){
            
        }
    }
</script>
<style lang="less" scoped>
    .market-foot{
        height: 1.1rem;
        width:100%;
        .footer{
            width:100%;
            height:1.1rem;
            position:fixed;
            bottom:0;
            left:0;
            background:#fff;
            box-shadow: 0 1px 5px #888;
            z-index:99999999;
            li{
                width:20%;
                text-align:center;
                .font{
                    font-size:.2rem;
                    color:#787878;
                }
                .icon{
                    height:.65rem;
                    position:relative;
                    display:block;
                    img{
                        position:absolute;
                        left:0;
                        top:0;
                        bottom:0;
                        right:0;
                        margin:auto;
                        margin-bottom:.1rem;
                    }
                }
                .icon.active{
                    display:none;
                }
            }
            li.isSelected{
                .font{
                    color:#ed3851;
                }
                .icon.active{
                    display:block;
                }    
                .icon{
                    display:none;
                }
            }
            li:nth-child(1){
                img{
                    width:.42rem;
                    height:.42rem;
                }
            }
            li:nth-child(2){
                img{
                    width:.42rem;
                    height:.42rem;
                }
            }
            li:nth-child(3){
                img{
                    width:.42rem;
                    height:.42rem;
                
                }
            }
            li:nth-child(4){
                img{
                    width:.42rem;
                    height:.42rem;
                }
            }
             li:nth-child(5){
                img{
                    width:.42rem;
                    height:.42rem;
                }
            }
        }
    }
</style>


